<template>
  <div>
    <h1>My shopping cart</h1>
    <router-link to="/hw">Go to HelloWorld</router-link>
    <el-button type="primary" @click="requestData" circle>
      getData
    </el-button>
    <el-table :data="resData" stripe style="width: 100%">
  <el-table-column prop="index" label="序号" width="180"></el-table-column>
  <el-table-column prop="pname" label="商品名称" width="180"></el-table-column>
  <el-table-column prop="price" label="价格" width="700"></el-table-column>
  <el-table-column prop="image" label="商品图片">
    <template slot-scope="scope">
      <img :src="scope.row.image" height="250" alt="">
    </template>
  </el-table-column>
</el-table>
  </div>
</template>

<script>
import axios from 'axios'

const instance = axios.create({
  baseURL: '/api',
  timeout: 1000,
})

export default {
  name: 'TestOne',
  data() {
    return {
      resData: [],
    }
  },

  mounted() {
    this.requestData()
  },

  methods: {
    requestData() {
  instance
    .get('goods/list')
    .then((res) => {
      this.resData = res.data.map((item, idx) => {
        return {
          index: idx + 1,
          pname: item.name,
          price: item.price1,
          image: `http://10.60.82.164:8080/${item.pthumbnail}`,
        }
      })
    })
    .catch((err) => {
      console.warn(err)
    })
},
}
}
</script>

<style scoped></style>